//crear galeria de imagenes con js

document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();

});

function iniciarApp(){
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija(){
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body')

    window.addEventListener('scroll', function(){
       if (sobreFestival.getBoundingClientRect().bottom < 0 ){
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else{
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }

    });

}



function scrollNav(){
    const enlaces = document.querySelectorAll('.navagacion-principal a');
    enlaces.forEach(enlace =>{
           enlace.addEventListener('click', function(e){
                e.preventDefault();

                const seccionScroll = e.target.attributes.href.value;
                const seccion = document.querySelector(seccionScroll);
                seccion.scrollIntoView({behavior: 'smooth'});
           }) 
    })
}


function crearGaleria(){
    const galeriaa = document.querySelector('.galeria-imagenes');

     for(let i = 1; i<=12; i++){
         const imagen = document.createElement('picture');
         imagen.innerHTML = `
         <source srcset="build/img/thumb/${i}.avif" type="imagen/avif">
              <source srcset="build/img/thumb/${i}.webp" type="imagen/webp">
              <img loading="Lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imgen galeria">
         `;

         //click a imagenes
         imagen.onclick = function(){
             mostrarImagen(i);
         }

         galeriaa.appendChild(imagen)
     }
}

function mostrarImagen(indice){
    const imagen = document.createElement('picture');
         imagen.innerHTML = `
         <source srcset="build/img/grande/${indice}.avif" type="imagen/avif">
              <source srcset="build/img/grande/${indice}.webp" type="imagen/webp">
              <img loading="Lazy" width="200" height="300" src="build/img/grande/${indice}.jpg" alt="imgen galeria">
         `;

         //crear Overlay con la imagen
         const overlay = document.createElement('DIV');
         overlay.appendChild(imagen);
         overlay.classList.add('overlay');
         overlay.onclick = function(){
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
            overlay.remove();
         }

         //Boton para cerrar el modal(ventana)de imagenes
         const cerrarModal = document.createElement('P');
         cerrarModal.textContent = 'X';
         cerrarModal.classList.add('btn-cerrar');
         cerrarModal.onclick = function(){
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
            overlay.remove();
            
        };
         overlay.appendChild(cerrarModal);


       
         //a√±adirlo al html
         const body = document.querySelector('body');
         body.appendChild(overlay);
         body.classList.add('fijar-body');
}